.email-icon {
	font-size: 18px;
	display: inline-block !important;
	vertical-align: middle;
	width: 18px;
	height: 18px;
}

.email-container {
	height: 40px;
	border-radius: 4px;
	margin-inline-end: 8px;
	margin-top: 16px;
	margin-bottom: 24px;
	line-height: 40px;
}

.container {
	max-width: 500px;
	position: absolute;
	top: 50%;
	transform: translate(-50%, -50%);
	left: 50%;
}

button {
	height: 40px !important;
}
